import PropTypes from 'prop-types';
import Box from '@mui/material/Box';
import { useRef, useEffect } from 'react';
import { Editor, EditorState } from 'draft-js';

/* import Divider from '@mui/material/Divider';
import EditorStyles from './EditorStyles'; */

export default function StoryEditor({
  storyError, editorState, handleEditorStateChange,
}) {
  const storyRef = useRef();

  useEffect(() => {
    storyRef.current.focus();
  }, []);

  return (
    <Box
      sx={{
        p: 2,
        m: 1,
        my: 2,
        border: 1,
        borderColor: storyError ? 'error.main' : 'primary.main',
        bgcolor: 'input.background',
        minHeight: '200px',
        borderRadius: 5,
      }}
      onClick={() => storyRef.current.focus()}
    >
      {/* <EditorStyles editorState={editorState} /> */}
      {/* <Divider sx={{ my: 1 }} />  */}
      <Editor
        editorState={editorState}
        onChange={handleEditorStateChange}
        placeholder="Tell your story..."
        ref={storyRef}
      />
    </Box>
  );
}

StoryEditor.propTypes = {
  storyError: PropTypes.string.isRequired,
  editorState: PropTypes.instanceOf(EditorState).isRequired,
  handleEditorStateChange: PropTypes.func.isRequired,
};
